//import Components Files
import './App.css';
import NavbarComp from './components/NavbarComp.js';
import Categ from './mainpage/Categ.js'
import Title1 from './mainpage/Title1.js'
import Title2 from './mainpage/Title2.js'
import Title3 from './mainpage/Title3.js'
import Title4 from './mainpage/Title4.js'
import Title5 from './mainpage/Title5.js'
import Banner1 from './mainpage/banner.js'
import Popular from './components/popularitems';
import Trending from './components/trendingitems';
import Newitems from './components/newitems'
import Info from './mainpage/Info.js'
import Footer from './components/footer.js'
import Slider from './mainpage/Slider.js'
import Season from './components/seasonitems.js'

//import subpage files
import Popularpage from './subpage/popularpage.js';
import Newpage from './subpage/newpage.js';
import Cart from './subpage/cart.js';
import Trendingpage from './subpage/trendingpage.js';
import Seasonpage from './subpage/seasonpage.js';
import About from './subpage/about.js';

//import jsonFiles
import categ from './jsondata/categories.js'
import popular from './jsondata/popularitems.js'
import trend from './jsondata/trenditems.js'
import newgood from './jsondata/newitems.js'
import info from './jsondata/infomation.js'
import season from './jsondata/seasonitems.js'

//import bootstrap Components
import { useState } from 'react';
import { Button, Navbar, Container, Nav, NavDropdown, Form} from 'react-bootstrap';
import { useNavigate, Outlet } from 'react-router-dom';
import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';

function App() {
  
  // useNavigate 
  let navigate = useNavigate();

  // Popular Selling 
  let [popitem, setPopitem] = useState(popular);
  let [popindex, setPopindex] = useState([0,1,2,3,4,5,6,7]);

  // Trend Items 
  let [trenditem, setTrenditem] = useState(trend);
  // let [trendindex, setTrendindex] = useState([0,1,2,3]);
  let [trendindex, setTrendindex] = useState([10,11,12,13]);

  // New Items 
  let [newitem, setNewitem] = useState(newgood);
  // let [newindex, setNewindex] = useState([0,1,2,3]);
  let [newindex, setNewindex] = useState([14,15,16,17]);

  // Season Items 
  let [seasonitem, setSeasonitem] = useState(season);
  // let [seasonindex, setSeasonindex] = useState([0,1,2,3]);
  let [seasonindex, setSeasonindex] = useState([18,19,20,21]);

  return (
    <div className="app">
      
      {/*  */}
      <NavbarComp/>
      
      <Routes>
        <Route path="/" element={
          <div>
          {/*   */}
          <div className="mainslider">
            <Slider/>
          </div>

          {/*  */}
          <div className="section1">
            <div className="container">
              <Title1/>
              <div className="row">
                {
                  categ.map((a,i)=>{
                    return(
                      <Categ categ={categ[i]}/>
                    )
                  })
                }
              </div>
            </div>
          </div>

          {/*  */}
          <div className="section2">
            <div className="container">
              <Title2/>
              <div className="row">
                {
                  popitem.map((a,i)=>{
                    return(
                      <Popular popitem={popitem[i]}
                      popindex={popindex} i={i}/>
                    )
                  })
                }
              </div>
            </div>
          </div>

          {/* Info */}
          <div className="section3">
            <div className="container">
              <div className="pic">
                <img src="./img/banner6.jpg"/>
              </div>
                <div className="row" style={{borderTop:"3px solid #ddd", marginTop:"25px"}}>
                  {
                    info.map((a,i)=>{
                      return(
                        <Info info={info[i]}/>
                      )
                    })
                  }
                </div>
            </div>
          </div>


          {/* */}
          <div className="section4">
            <div className="container">
              <Title3/>
              <div className="row">
                {
                  trenditem.map((a,i)=>{
                    return(
                      <Trending trenditem={trenditem[i]}
                      trendindex={trendindex} i={i}/>
                    )
                  })
                }
              </div>
            </div>
          </div>

          {/*  */}
          <Banner1/>

          {/*   */}
          <div className="section6">
            <div className="container">
              <Title4/>
              <div className="row">
                {
                  newitem.map((a,i)=>{
                    return(
                      <Newitems newitem={newitem[i]}
                      newindex={newindex} i={i}/>
                    )
                  })
                }
              </div>
            </div>
          </div>

          {/* Image slider*/}
          <div className="slider3">
                <div className="slidertext">
                  <h4>Want more Information?</h4>
                  <p style={{marginBottom:"40px"}}>You can see more information what you looking for.</p>
                  <Nav.Link onClick={()=>{navigate('/about')}}>
                    <Button variant="primary" style={{borderRadius:"5px", width:"120px"}}>About Us</Button>
                  </Nav.Link>
                </div>
          </div>
        </div>
      }/>

        {/*   */}
        <Route path="/popularitems/:id" element={<Popularpage popitem={popitem}/>} />

        {/*   */}
        <Route path="/trenditems/:id" element={<Trendingpage trenditem={trenditem}/>}/>

        {/*   */}
        <Route path="/newitems/:id" element={<Newpage newitem={newitem}/>} />

        {/*   */}
        <Route path="/seasonitems/:id" element={<Seasonpage seasonitem={seasonitem}/>} />

        {/*   */}
        <Route path="/cart" element={<Cart/>}/>

        {/* About  */}
        <Route path="/about" element={<About/>}/>
        
      </Routes>

      {/* Footer */}
      
      <div className="section7">
      <Footer/>
        <div className="copyright">
            <p style={{textAlign:"center", marginTop:"15px"}}>
            @ Copyright by alista in 2022. Made by alista
            </p>
        </div>
      </div>
    </div>
  );
}

export default App;
