import Carousel from 'react-bootstrap/Carousel';
import { Button } from 'react-bootstrap';

function Slider(){
    return(
        <>
    <Carousel>
      <Carousel.Item>
        <img
          className="d-block w-100"
          height="600px"
          src="./img/slider7.jpg"
          alt="First slide"
        />
        <Carousel.Caption>
          <div className="col-md-5" style={{marginBottom:"10%", marginLeft:"10%"}}>
            <h3 style={{fontSize:"45px"}}>100% Fresh and Natural</h3>
            <p style={{fontSize:"20px"}}>
              We've been squeezing juice in alista <br/>
              for over 20years, and we are proud.
            </p>
            <Button variant="light">Shop now</Button>
          </div>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          height="600px"
          src="./img/slider8.jpg"
          alt="Second slide"
        />

        <Carousel.Caption>
          <div className="col-md-5" style={{marginBottom:"10%", marginLeft:"10%"}}>
            <h3 style={{fontSize:"45px"}}>Nurturing the skin's</h3>
            <p style={{fontSize:"20px"}}>
              Learn more about our product philosophy, and<br/>
              the benefits of microbiome-friendly skincare.
            </p>
            <Button variant="light">Shop now</Button>
          </div>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
        </>
    );
}
export default Slider;