import { getDefaultNormalizer } from '@testing-library/react';
import { useState } from 'react';
import { Button, Navbar, Container, Nav, NavDropdown, Form, ListGroupItem} from 'react-bootstrap'
import { useNavigate, Outlet } from 'react-router-dom';
import { BrowserRouter, Routes, Route, Link, useParams} from 'react-router-dom';
import {Col, Row, Tab, Tabs, Table, ListGroup} from 'react-bootstrap';
import { addItem } from '../components/store.js'
import { useDispatch } from 'react-redux'

//import jsonFiles
import newgood from '../jsondata/newitems.js'
import trend from '../jsondata/trenditems.js'

//import Components Files
import Trending from '../components/trendingitems.js'
import Newitems from '../components/newitems.js';
import Title3 from '../mainpage/Title3.js'
import Title4 from '../mainpage/Title4.js'



function Seasonpage(props) {
    let {id} = useParams();
    // console.log(id);
    // console.log(Number(id)+1);

    // Season items 
    let [trenditem, setTrenditem] = useState(trend);
    let [trendindex, setTrendindex] = useState([10,11,12,13]);

    // New items 
    let [newitem, setNewitem] = useState(newgood);
    let [newindex, setNewindex] = useState([14,15,16,17]);
    
    //redux parameter
    let seasonproduct  = props.seasonitem.find((x) => x.id == id )
    let dispatch = useDispatch()    

    return (
        <>
            <div class="subnav">
                <div className="container">
                    <ul style={{display:"flex", textAlign:"center"}}>
                        <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>Home</li>
                        <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>&gt;</li>
                        <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>Organics</li>
                        <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>&gt;</li>
                        <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>Season Items</li>
                    </ul>
                </div>
            </div>
            <div class="subsection" >
            <div className="container" style={{ marginTop:"-16px", padding:"20px"}}>
                <div className="row">
                    <div className="col-md-6">
                        <img src={'/'+seasonproduct.imgUrl} width="80%" />
                        {/* <img src={'../img/seasonitem' + (Number(id)+1) + '.jpg'}  width="80%" /> */}
                    </div>
                    <div className="col-md-6">
                        <h4 className="pt-5" 
                        style={{width:"550px", fontWeight:"bold", marginTop:"-30px", 
                        lineHeight:"50px", borderBottom:"1px solid #ddd"}}>
                        {seasonproduct.title}</h4>
                        {/* {props.seasonitem[id].title}</h4> */}
                        <p style={{lineHeight:"30px"}}>
                        Pellentesque habitant morbi tristique senectus et netus et malesuada<br/>
                        fames ac turpis egestas. Vestibulum tortor quam, feugiat vitae, ultricies<br/>
                        eget, tempor sit amet, ante.   
                        </p>
                        <p>{seasonproduct.content}</p>
                        {/* <p>{props.seasonitem[id].content}</p> */}
                        <ul>
                            <li style={{listStyle:"disc"}}>Water-resistant fabric with soft lycra detailing inside</li>
                            <li style={{listStyle:"disc"}}>Clean zip-front, and three piece hood</li>
                            <li style={{listStyle:"disc"}}>Subtle branding and diagonal panel detail</li>
                        </ul>
                        <p style={{fontWeight:"bold", color:"#84B53C", width:"550px", 
                        borderTop:"1px solid #ddd", lineHeight:"50px"}}>
                        {seasonproduct.price} &#x20a9;</p>
                        {/* {props.seasonitem[id].price} &#x20a9;</p> */}
                        <button className="btn btn-danger" style={{marginRight:"20px"}}>Add to Wishlist</button>
                        <button className="btn btn-primary" onClick={()=>{
                            dispatch(addItem({
                                id : seasonproduct.id,
                                img : <img src={'/'+seasonproduct.imgUrl} width="120px" height="120px" />,
                                name : seasonproduct.title,
                                quantity : 1,
                                price : seasonproduct.price
                            }))
                        }}>Add to Cart</button>
                        {/* <button className="btn btn-primary">Add to Cart</button> */}
                    </div>
                    
                    <div className="col-md-6" style={{width:"100%", marginTop:"50px"}}>
                        <div className="container"> 
                            <Tabs
                            defaultActiveKey="description"
                            id="fill-justify"
                            className="mb-3"
                            fill>
                            <Tab eventKey="description" title="Description" style={{fontSize:"16px"}}>
                                <p>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam fringilla augue nec est tristique auctor. Donec non est at libero vulputate rutrum.<br/>
                                Morbi ornare lectus quis justo gravida semper. Nulla tellus mi, vulputate adipiscing cursus eu, suscipit id nulla.<br/><br/>

                                Pellentesque aliquet, sem eget laoreet ultrices, ipsum metus feugiat sem, quis fermentum turpis eros eget velit. Donec ac tempus ante.<br/>
                                Fusce ultricies massa massa. Fusce aliquam, purus eget sagittis vulputate, sapien libero hendrerit est, sed commodo augue nisi non neque.<br/>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed tempor, lorem et placerat vestibulum, metus nisi posuere nisl, in accumsan elit odio quis mi.<br/>
                                Cras neque metus, consequat et blandit et, luctus a nunc. Etiam gravida vehicula tellus, in imperdiet ligula euismod eget.<br/>
                                </p>
                            </Tab>
                            <Tab eventKey="about" title="About">
                            
                            <table class="about_tb">
                                <tr>
                                    <th>Composition</th>
                                    <td>Season Items</td>
                                </tr>
                                <tr>
                                    <th>Style</th>
                                    <td>Fresh</td>
                                </tr>
                                <tr>
                                    <th>Properties</th>
                                    <td>Organics</td>
                                </tr>
                            </table>

                                <p style={{fontSize:"15px", marginTop:"20px"}}>
                                Fashion has been creating well-designed collections since 2010.<br/>
                                The brand offers feminine designs delivering stylish separates and statement dresses which have since evolved into a full ready-to-wear collection<br/>
                                in which every item is a vital part of a woman's wardrobe. The result? Cool, easy, chic looks with youthful elegance and unmistakable signature style.<br/>
                                All the beautiful pieces are made in Italy and manufactured with the greatest attention. Now Fashion extends to a range of accessories including shoes, hats, belts and more! 
                                </p>
                            </Tab>
                            <Tab eventKey="reviews" title="Reviews (2)">
                                <h4 style={{marginBottom:"40px"}}>Review for Popular Items</h4>
                                <div style={{borderTop:"1px solid #ddd", borderBottom:"1px solid #ddd", padding:"15px 0 0 0px",display:"flex", justifyContent:"space-between"}}>
                                    <img src="../img/person.jpg" style={{width:"100px", height:"100px"}}/>
                                    <div>
                                    <h5>Jane Doe</h5>
                                    <span  style={{fontSize:"13px"}}>Date: 2022/09/23</span>                             
                                    <p style={{padding:"10px 0px"}}>
                                    Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old.<br/>
                                    Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur.
                                    </p>
                                    </div>
                                </div>

                                <div style={{borderBottom:"1px solid #ddd", padding:"15px 0 0 0px", display:"flex", justifyContent:"space-between"}}>
                                    <img src="../img/person.jpg" style={{width:"100px", height:"100px"}}/>
                                    <div>
                                    <h5>Jone Doe</h5>
                                    <span style={{fontSize:"13px"}}>Date: 2022/09/25</span>                             
                                    <p style={{padding:"10px 0px"}}>
                                    Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old.<br/>
                                    Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur.
                                    </p>
                                    </div>
                                </div>

                                <Form style={{marginTop:"30px"}}>
                                    <Form.Group className="mb-3">
                                        <Form.Label>Your Email</Form.Label>
                                        <Form.Control type="email" placeholder="name@example.com" />
                                    </Form.Group>
                                    <Form.Group className="mb-3">
                                        <Form.Label>Your Name</Form.Label>
                                        <Form.Control type="name" placeholder="John Doe" />
                                    </Form.Group>
                                    <Form.Group className="mb-3">
                                        <Form.Label>Example textarea</Form.Label>
                                        <Form.Control as="textarea" placeholder="Fill your opinion..." rows={5} />
                                    </Form.Group>
                                    <Button variant='primary' style={{float:"right", marginBottom:"10px"}}>Comment</Button>
                                </Form>
                            </Tab>
                            </Tabs>
                        </div>
                    </div>
                    <div className="section4">
                        <div className="container">
                        <Title3/>
                        <div className="row">
                            {
                            trenditem.map((a,i)=>{
                                return(
                                <Trending trenditem={trenditem[i]}
                                trendindex={trendindex} i={i}/>
                                )
                            })
                            }
                        </div>
                        </div>
                    </div>
                    <div className="section6">
                        <div className="container">
                            <Title4/>
                            <div className="row" style={{padding:"10px 10px 0 10px"}}>
                                {
                                newitem.map((a,i)=>{
                                    return(
                                    <Newitems newitem={newitem[i]}
                                    newindex={newindex} i={i}/>
                                    )
                                })
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}


export default Seasonpage;
