//import bootstrap Components
import {Button, Navbar, Container, Nav, NavDropdown, Form} from 'react-bootstrap';
import {useNavigate} from 'react-router-dom';

function NavbarComp() {

    let navigate = useNavigate();

        return (
            <div>
                <Navbar bg="dark" variant="dark" expand="lg">
                <Container>
                    <Navbar.Brand href="/">
                        <img src="../img/logo.jpg" alt=""/>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="navbarScroll" />
                    <Navbar.Collapse id="navbarScroll">
                    <Nav
                        className="me-auto my-2 my-lg-0"
                        style={{ maxHeight: 'auto'}}
                        navbarScroll
                    >
                        
                        <Nav.Link onClick={()=>{ navigate('/')}}>Home</Nav.Link>
                        {/* <Nav.Link href="/home">Home</Nav.Link> */}
                        <NavDropdown title="Shop" id="navbarScrollingDropdown">
                            <NavDropdown.Item>
                                <Nav.Link onClick={()=>{navigate('/popularitems/0')}} style={{color:"black", fontSize:"15px"}}> Popular Items</Nav.Link>
                            </NavDropdown.Item>
                            <NavDropdown.Item>
                                <Nav.Link onClick={()=>{navigate('/trenditems/10')}} style={{color:"black", fontSize:"15px"}}>Trending Items</Nav.Link>
                            </NavDropdown.Item>
                            <NavDropdown.Item>
                                <Nav.Link onClick={()=>{navigate('/newitems/14')}} style={{color:"black", fontSize:"15px"}}>New Items</Nav.Link>
                            </NavDropdown.Item>
                            <NavDropdown.Item>
                                <Nav.Link onClick={()=>{ navigate('/seasonitems/18')}} style={{color:"black", fontSize:"15px"}}>Season Items</Nav.Link>
                            </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link onClick={()=>{navigate('/cart')}}>Cart</Nav.Link>
                        <Nav.Link onClick={()=>{navigate('/about')}}>About us</Nav.Link>
                    </Nav>
                    <Form className="d-flex">
                        <Form.Control
                        type="search"
                        placeholder="Search"
                        className="me-2"
                        aria-label="Search"
                        />
                        <Button variant="outline-light">Search</Button>
                    </Form>
                    <div className='my-3'>
                    <Button variant="outline-light" className="mx-2">Login</Button>
                    <Button variant="outline-light">Sign Up</Button>
                    </div>
                    </Navbar.Collapse>
                </Container>
                </Navbar>
            </div>
        );
    }

export default NavbarComp;