import { useNavigate } from 'react-router-dom';
import { Nav } from 'react-bootstrap'

function Info (props){

    let navigate = useNavigate();
  
    return(
    <div className="col-md-3" style={{marginTop:"30px"}}>
      <div className="container">
        <div className="icon">
          <img src={props.info.imgUrl} width="30%" style={{marginBottom:"25px"}}></img>
        </div>
        <h5 style={{fontWeight:"bold", textAlign:"center", color:""}}>{props.info.title}</h5>
        <p style={{textAlign:"center"}}>{props.info.content}</p>
      </div>
    </div>
    )
  }

  export default Info;