import { useNavigate } from 'react-router-dom';
import { Nav, Button } from 'react-bootstrap'

function Trending (props){

    let navigate = useNavigate();
    
    return(
      <div className="col-md-3">
        <Nav.Link onClick={() => {navigate('/trenditems/'+ props.trendindex[props.i]) }} className="tre">
            <div className="imgbox">
              <img src={props.trenditem.imgUrl} width="100%"/>
            </div>
            <p
            style={{
              fontSize:"18px",
              color:"#333"
            }}
            >{props.trenditem.content}</p>
            <p
            style={{
              fontSize:"22px",
              color:"#333"
            }}
            >{props.trenditem.title}</p>
            <span
            style={{
              fontSize:"18px",
              fontWeight:"bold",
              color:"#84B53C"
            }}
            >{props.trenditem.price} &#x20a9;</span>
        
        <div className="container" style={{marginTop:"20px"}}>
          <div className="col-md-12">
            <Button className="btn" style={
              {width:"100%", 
               marginBottom:"10px",
               color:"#fff",
               fontWeight:"bold"}
              }>Buy Now</Button>
          </div>
        </div>
        
        </Nav.Link>
        
      </div>

    )
  }
  
  export default Trending;