import { useNavigate } from 'react-router-dom';
import { Nav, Button } from 'react-bootstrap';


function Popular (props) {

    let navigate = useNavigate();

    return (
      <div className="col-md-3">
        <Nav.Link onClick={() => {navigate('/popularitems/'+ props.popindex[props.i]) }} className="pop">
            <div className="imgbox">
              <img src={props.popitem.imgUrl} width="100%"/>
            </div>
            <p
            style={{
              fontSize:"18px",
              color:"#333"
            }}
            >{props.popitem.content}</p>
            <p
            style={{
              fontSize:"22px",
              color:"#333"
            }}
            >{props.popitem.title}</p>
            <span
            style={{
              fontSize:"18px",
              fontWeight:"bold",
              color:"#84B53C"
            }}
            >{props.popitem.price} &#x20a9;</span>

        <div className="container" style={{marginTop:"20px"}}>
          <div className="col-md-12">
            <Button className="btn" style={
              {width:"100%", 
               marginBottom:"10px",
               color:"#fff",
               fontWeight:"bold"}
              }>Buy Now</Button>
          </div>
        </div>

        </Nav.Link>

      </div>
      
    );
  }
  
  export default Popular;