import {Table, Form} from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import { addCount, minusCount, deleteCount} from '../components/store.js'
import { Button } from 'react-bootstrap'

function Cart(){

  let cart = useSelector( state => state );
  // console.log(state.cart[0].name);

  // dispatch  store.js   
  let dispatch = useDispatch()

    return(
    <>
      <div class="subnav">
        <div className="container">
          <ul style={{display:"flex", textAlign:"center"}}>
            <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>Home</li>
            <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>&gt;</li>
            <li style={{margin:"0 20px 0 0", padding:"5px 0"}}>Cart</li>
          </ul>
        </div>
      </div>

      <div className="container">
      
        <div className="container">
          <Table className="cart_table">
            <thead>
              <tr>
                <th>Image</th>
                <th>Product</th>
                <th>Quantity</th>
                <th>price</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              {cart.basket.map((a, i) => (
                <tr key={i} style={{borderBottom:"1px solid #ddd"}}>
                  <td>{cart.basket[i].img}</td>
                  <td>
                    <span style={{display:"flex", justifyContent:"space-evenly", marginTop:"50px"}}>
                      {cart.basket[i].name}
                    </span>
                  </td>
                  <td>
                    <span style={{display:"flex", justifyContent:"space-evenly", marginTop:"50px"}}>
                  <Button variant='secondary' onClick={() => {
                      dispatch(addCount(cart.basket[i].id));
                    }} style={{width:"25px", height:"25px", padding:"0"}}> + </Button>
                      {cart.basket[i].quantity}
                    <Button variant='secondary' onClick={() => {
                      dispatch(minusCount(cart.basket[i].id));
                    }} style={{width:"25px", height:"25px", padding:"0"}}> - </Button>
                    </span>
                  </td>
                  <td style={{borderRight:"1px solid #ddd"}}>
                    <span style={{display:"flex", justifyContent:"space-evenly", marginTop:"50px"}}>
                      {cart.basket[i].price}
                    </span>
                  </td>
                    <button variant='secondary' onClick={(e) => {
                      dispatch(deleteCount(e.target.parentElement));
                    }}  style={{width:"40px", height:"40px", padding:"5px", fontSize:"20px", border:"none", marginTop:"50px"}}>
                      &#215;
                    </button>
                </tr>
              ))}
            </tbody>
          </Table> 
        </div>

        <div className="container" style={{display:"flex", justifyContent:"space-evenly", margin:"20px auto"}}>
          <div class="coupon" style={{border:"1px solid #eee", width:"40%", height:"100%"}}>
              <h4 style={{padding:"15px", background:"#bfdf91", color:"#fff"}}>Coupon</h4>
              <p style={{padding:"5px 15px 0px 15px"}}>Enter your coupon code if you have one.</p>
              <Form variant="success" style={{padding:"0px 15px 15px 15px"}}>
                <Form.Group className="md-3" style={{display:"flex", justifyContent:"space-between"}}>
                  <Form.Control type="text" placeholder="coupon code" style={{width:"70%"}}/>
                  <Button variant='outline-success'>Apply Coupon</Button>
                </Form.Group>
              </Form>
          </div>
          <div class="total" style={{border:"1px solid #eee", width:"50%", height:"100%"}}>
            <h4 style={{padding:"15px", background:"#bfdf91", color:"#fff"}}>Total Price</h4>
            <div className="container" style={{width:"90%"}}>
              <table class="about_tb2">
                <tr>
                  <th style={{borderBottom:"none"}}>Subtotal</th>
                  <td style={{borderLeft:"none", borderBottom:"none"}}><span style={{float:"right"}}>48000 &#8361;</span></td>
                </tr>
                <tr>
                  <th>Shipping</th>
                  <td style={{borderLeft:"none"}}><span style={{float:"right"}}>4000 &#8361;</span></td>
                </tr>
                <tr>
                  <th>Total</th>
                  <td style={{borderLeft:"none"}}><span style={{float:"right"}}>52000 &#8361;</span></td>
                </tr>
              </table>
              <Button variant="outline-success" style={{float:"right", margin:"15px"}}>Proceed to Checkout</Button>
            </div>
          </div>
        </div>

      </div>
    </>

   )

}

export default Cart