const Title4 = ()=>{
    let title4 = {
        marginTop:"70px",
        paddingTop:"20px",
        textAlign:'center',
        fontWeight:"bold"
    }
    return(
            <>
            <h3 style={title4}>New Products</h3>
            <p style={{textAlign:'center'}}>Browse the collection of our new products</p>
            </>
    )
  }

  export default Title4;
  