const Title3 = ()=>{
    let title3 = {
        marginTop:"70px",
        paddingTop:"20px",
        textAlign:'center',
        fontWeight:"bold"
    }
    return(
      <>
      <h3 style={title3}>Trending Items</h3>
      <p style={{textAlign:'center'}}>Browse the collection of our trending products</p>
      </>
    )
  }

  export default Title3;
  