import { configureStore, createSlice } from '@reduxjs/toolkit'

let basket = createSlice({
  name: "basket",
  initialState: [
  {
    id: 8,
    img:<img src="./img/product9.jpg" style={{width:"120px", height:"120px"}} alt="id_2"/>,
    name: "Organic Orange Juice",
    quantity: 1,
    price: 3500
  },
  {
    id: 9,
    img:<img src="./img/product10.jpg" style={{width:"120px", height:"120px"}} alt="id_1"/>,
    name: "Cherry Tomato",
    quantity: 1,
    price: 3200
  }
  ],
  reducers: {
    addCount(state, action) {
      let nums = state.findIndex( a => a.id === action.payload);
      state[nums].quantity++;
    },
    minusCount(state, action) {
      let nums = state.findIndex( a => a.id === action.payload);
      if (state[nums].quantity > 1)  state[nums].quantity--;
    },
    addItem(state, action){
      state.push(action.payload)
    },
    deleteCount(state, action) {
      action.payload.remove();
    },
  },
});

// addCount(1)

export let { addCount, minusCount, addItem, deleteCount} = basket.actions

export default configureStore({
  reducer: {
    basket : basket.reducer
  }
}) 